import { Injectable } from "@angular/core";
import { Resolve, ActivatedRouteSnapshot, RouterStateSnapshot, ActivatedRoute, Router } from "@angular/router";
import { Observable } from "rxjs";
import { BehaviorSubject } from 'rxjs';
import { Subscription } from 'rxjs';
import { User } from '../interface.service';
import{MyAccountService} from './my-account.service'



@Injectable({ providedIn: 'root' })
export class StudentResolve implements Resolve<User> {
    user:User
    constructor(route: ActivatedRoute,private myAccountService:MyAccountService,private router: Router) { }



    dataSource = new BehaviorSubject<any>(null);


    resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<User> | Promise<any> | any {


        console.log("state", state.url)
        console.log("idd :::", route.params['id'])
        return this.myAccountService.getUserService(route.params['id'])


    }


}





